import { graphql, useStaticQuery } from 'gatsby'
import React, { useMemo } from 'react'
import { getImage } from "gatsby-plugin-image"

import Banner from './Banner'

const DesktopBanner = () => {
  const { allDesktopJson: { nodes } } = useStaticQuery(graphql`
    query DesktopBannerQuery {
      allDesktopJson {
        nodes {
          name
          width
          height
          x
          y
          depth
          image {
            childImageSharp {
              gatsbyImageData(
                layout: FULL_WIDTH
              )
            }
          }
        }
      }
    }
  `)
  const images = useMemo(() => nodes.map(layer => ({
    ...layer,
    image: getImage(layer.image),
  })).reverse(), [nodes])

  return (
    <Banner
      key="desktop"
      width={2560}
      height={1140}
      images={images}
    />
  )
}

export default DesktopBanner
